.container {
    margin-top: 100px;
    padding: 20px;

    @media screen and (max-width: 546px) {
        margin-top: 10px;
    }

    @media (min-width: 547px) and (max-width: 850pxpx) {
        margin-top: 30px;
    }
}

.row {
    display: flex;
    gap: 20px;
}

.col1 {
    flex: 1;
}

.col3 {
    flex: 3;
}

.card {
    background-color: #ffffff;
    border-radius: 12px;
    padding: 20px;
    padding-bottom: 150px;
    box-shadow: 0px 0px 19px -8px rgba(121, 134, 147, 0.694);
    text-align: center;

    @media screen and (max-width: 546px) {
        margin-top: 40px;
        padding-bottom: 28px;
    }
}

h3 {
    font-size: 1.5rem;
    margin-bottom: 10px;
}

p,
ul {
    font-size: 1rem;
    color: #555;
}

ul {
    list-style: none;
    padding: 0;
}

ul li {
    margin-bottom: 8px;
}

@media (max-width: 768px) {
    .row {
        flex-direction: column;
    }

    .col1,
    .col3 {
        flex: none;
        width: 100%;
    }
}

.weatherCardBg {
    background-image: url('../../assets/images/undersea.png');
    background-position: center;
    background-size: cover;
    padding-bottom: 150px;
}

.eventCardBg {
    background-image: url('../../assets/images/bgfish.png');
    background-position: center;
    background-size: cover;
    position: relative;

    &::after {
        content: url('../../assets/images/hat.png');
        position: absolute;
        top: -750px;
        right: -600px;
        z-index: 100;
        transform: scale(0.15);
    }

    @media screen and (max-width: 546px) {
        &::after {
            top: -730px;
            right: -680px;
            transform: scale(0.1);
        }
    }

    @media (min-width: 547px) and (max-width: 850pxpx) {
        &::after {
            top: -730px;
            right: -850px;
            transform: scale(0.1);
        }
    }
}