@import '../../styles/utils/variables';

.popularCategories {
    padding: 20px;
  }
  
  .header {
    text-align: center;
    margin-bottom: 20px;
    margin-top: 50px;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      left: 150px;
      width: 150px;     
      height: 150px;    
      background-image: url('../../assets/images/starsea.png');
      background-size: contain;
      background-repeat: no-repeat;
    }
    @media screen and (max-width: 768px) {
      font-size: 24px;
      &::after {
        position: absolute;
        z-index: -100;
        top: 20px;
        left: -30px;
        width: 100px;
        height: 100px;
      }
      
    }
  
    h2 {
      font-size: 24px;
      font-weight: $fw-300;
      color: $color-primary-heading; 
    }
  }
  
  .content {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .cardContainer {
    display: flex;
    gap: 20px;
    overflow-x: auto;
    padding: 10px;
  }
  
  .card {
    width: 200px;
    height: 300px;
    background-color: #f0f0f0;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  
    img {
      width: 100%;
      height: 150px;
      object-fit: cover;
      border-radius: 5px;
    }
  
    h3 {
      margin-top: 10px;
      font-size: 16px;
      font-weight: 500;
    }
  }
  
  .categoryImage {
    width: 100%; 
    height: auto; 
    max-width: 300px;
    @media screen and (max-width: 768px) {
      width: 100%;
      max-width: 300px;
      
    }
  }