@import '../utils/variables';

body {
  font-family: $font-family-base;
  font-size: $font-size-base;
  color: $color-primary-text;
  margin: $spacing-0;
  padding: $spacing-0;
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: $color-primary-text;

  &:hover {
    color: darken($color-primary-text, 10%);
  }
}