.container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    text-align: center;
  }
  
  .title {
    padding-top: 100px;
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 10px;
    color: #333;
  }
  
  .description {
    font-size: 1rem;
    color: #666;
    margin-bottom: 20px;
  }
  
  .categories {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .category {
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    background: #f9f9f9;
    transition: all 0.3s ease-in-out;
  }
  
  .category:hover {
    background: #f1f1f1;
  }
  
  .categoryTitle {
    width: 100%;
    padding: 12px 16px;
    font-size: 1.2rem;
    font-weight: bold;
    background: linear-gradient(45deg, #00D0FF, #007c00);
    color: white;
    border: none;
    cursor: pointer;
    text-align: left;
    transition: background 0.3s;
  }
  
  .categoryTitle:hover {
    background: linear-gradient(45deg, #06839f, #007c00);
  }
  
  .itemList {
    text-align: start;
    list-style: none;
    padding: 10px 20px;
    margin: 0;
  }
  
  .item {
    padding: 8px 0;
    font-size: 14px;
    color: #434343;
    border-bottom: 1px solid #ddd;
  }
  
  .item:last-child {
    border-bottom: none;
  }
  