// colors

$color-primary-heading: #00A0C4;
$color-primary-text: #9FA1A1 ;
$color-primary-card-title: #667C81;
$color-primary-frame: #E9E9E9;
$color-link: #00A0C4;
$color-menu-bg:#ffffff22;
$color-menu-shadow:  #00000008;
$link-hover-color: #00718b;

// bg-colors

$bg-color-btn-primary: #F5F5F5;

// fonts

$font-family-base: "Poppins", serif;

// Font Weights

$fw-100: 100;
$fw-200: 200;
$fw-300: 300;
$fw-400: 400;
$fw-500: 500;
$fw-600: 600;
$fw-700: 700;
$fw-800: 800;
$fw-900: 900;

// Font Sizes

$font-size-sm: 14px ;
$font-size-base: 16px;
$font-size-h1: 2.5rem;
$font-size-h2: 2rem;
$font-size-h3: 1.75rem;
$font-size-h4: 1.5rem;
$font-size-h5: 1.25rem;
$font-size-h6: 1rem;

// Breakpoints

$breakpoint-sm: 576px;
$breakpoint-md: 768px;
$breakpoint-lg: 992px;

// Spacing
$spacing-0: 0;
$spacing-1: 0.25rem;
$spacing-2: 0.5rem;
$spacing-3: 1rem;
$spacing-4: 1.5rem;
$spacing-5: 3rem;
$spacing-6: 4rem;
$spacing-7: 5rem;
$spacing-8: 6rem;
$spacing-9: 7rem;
$spacing-10: 8rem;

// Z-Index

$z-index-1: 100;
$z-index-2: 200;
$z-index-3: 300;
$z-index-4: 400;
$z-index-5: 500;
$z-index-6: 600;
$z-index-7: 700;
$z-index-8: 800;
$z-index-9: 900;
$z-index-10: 1000;