@import "../../styles/utils/_variables.scss";

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9999;
  backdrop-filter: blur(8px);
  background-color: $color-menu-bg;
  box-shadow: $spacing-0 $spacing-1 $spacing-2 $color-menu-shadow;
  height: $spacing-5;
  display: flex;
  align-items: center;
}

.headerContent {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  width: 90%;
  margin: $spacing-0 auto;
  height: 100%;

  h6 {
    margin: $spacing-0;
  }

  nav {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;

    ul {
      list-style: none;
      display: flex;
      gap: $spacing-3;
      align-items: center;
      margin: $spacing-0;
      padding: $spacing-0;

      li {
        margin: $spacing-0;

        a {
          text-decoration: none;
          color: $color-link;
          font-size: $font-size-sm;
          font-weight: $fw-300;
          transition: color 0.3s;

          &:hover {
            color: $link-hover-color;
            text-decoration: none;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .headerContent {
    flex-direction: row;
    justify-content: space-between;
    padding: $spacing-0 $spacing-4;

    nav {
      display: none;
      /* Varsayılan olarak gizli */
    }
  }

  .navMenuActive {
    display: flex !important;
    position: fixed;
    /* Sayfaya göre konumlandır */
    top: $spacing-5;
    left: 0;
    flex-direction: column;
    background-color: $color-menu-bg;
    width: 100%;
    box-shadow: $spacing-0 $spacing-1 $spacing-2 $color-menu-shadow;
    padding: $spacing-4;
    gap: $spacing-3;

    ul {
      list-style: none;
      margin: 0;
      padding: 0;

      li {
        text-align: center;

        a {
          font-size: $font-size-sm;
          color: $color-link;
          text-decoration: none;
          display: block;
          padding: $spacing-1 $spacing-0;

          &:hover {
            color: $link-hover-color;
          }
        }
      }
    }
  }
}

.hamburgerMenu {
  display: none;

  @media screen and (max-width: 768px) {
    display: block;
    font-size: $font-size-h4;
    cursor: pointer;
    position: relative;
  }
}