@import '../utils/variables';
$breakpoints: (
  small: 576px,
  medium: 768px,
  large: 992px,
  xlarge: 1200px,
  xxlarge: 1400px
);

@mixin respond-to($breakpoint) {
  @if map-has-key($breakpoints, $breakpoint) {
    @media (min-width: map-get($breakpoints, $breakpoint)) {
      @content;
    }
  }
}

// Container ve Row Genel Stilleri
.container {
  width: 100%;
  padding-top: $spacing-6;
  margin: 0 auto;
  display: flex;
  justify-content: center;

  @include respond-to(small) {
    max-width: 540px;
  }
  @include respond-to(medium) {
    max-width: 720px;
  }
  @include respond-to(large) {
    max-width: 960px;
  }
  @include respond-to(xlarge) {
    max-width: 1140px;
  }
  @include respond-to(xxlarge) {
    max-width: 1320px;
  }
}

.container-fluid {
  width: 100%;
  padding: $spacing-0 $spacing-0;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -0.5rem;
  margin-left: -0.5rem;
}

// Colons for responsive grid system
.col {
  flex: 1 1 0;
  padding: 0 0.5rem;

  // Small Screens
  @include respond-to(small) {
    &-sm { flex: 0 0 100%; max-width: 100%; }
  }

  // Medium Screens
  @include respond-to(medium) {
    &-md-12 { flex: 0 0 100%; max-width: 100%; }
    &-md-6 { flex: 0 0 50%; max-width: 50%; }
    &-md-4 { flex: 0 0 33.333%; max-width: 33.333%; }
    &-md-3 { flex: 0 0 25%; max-width: 25%; }
  }

  // Large Screens
  @include respond-to(large) {
    &-lg-12 { flex: 0 0 100%; max-width: 100%; }
    &-lg-6 { flex: 0 0 50%; max-width: 50%; }
    &-lg-4 { flex: 0 0 33.333%; max-width: 33.333%; }
    &-lg-3 { flex: 0 0 25%; max-width: 25%; }
  }

  // Extra Large Screens
  @include respond-to(xlarge) {
    &-xl-12 { flex: 0 0 100%; max-width: 100%; }
    &-xl-6 { flex: 0 0 50%; max-width: 50%; }
    &-xl-4 { flex: 0 0 33.333%; max-width: 33.333%; }
    &-xl-3 { flex: 0 0 25%; max-width: 25%; }
  }

  // Extra Extra Large Screens
  @include respond-to(xxlarge) {
    &-xxl-12 { flex: 0 0 100%; max-width: 100%; }
    &-xxl-6 { flex: 0 0 50%; max-width: 50%; }
    &-xxl-4 { flex: 0 0 33.333%; max-width: 33.333%; }
    &-xxl-3 { flex: 0 0 25%; max-width: 25%; }
  }

  // Default (Mobile first) Styles
  &-12 { flex: 0 0 100%; max-width: 100%; }
  &-6 { flex: 0 0 50%; max-width: 50%; }
  &-4 { flex: 0 0 33.333%; max-width: 33.333%; }
  &-3 { flex: 0 0 25%; max-width: 25%; }
  &-2 { flex: 0 0 16.666%; max-width: 16.666%; }
  &-1 { flex: 0 0 8.333%; max-width: 8.333%; }
}
