@import "../../styles/utils/variables";

.home {
  .heroSection {
    height: 100vh;
    background-image: url("../../assets/images/herosection-bg.png");
    background-size: cover;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 5rem;

    .content {
      width: 100%;
      max-width: 1200px;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;

      @media (max-width: 768px) {
        flex-direction: column;
        text-align: center;
      }

      .imageAndText {
        flex: 1;
        align-items: center;

        @media (max-width: 768px) {
          flex-direction: column;
        }

        .heroImage {
          width: 35%;
          object-fit: cover;
          margin-right: 2rem;

          @media (max-width: 768px) {
            width: 80%;
            margin: 0 0 1rem 0;
            margin-top: 50px;
          }
        }

        .text {
          // flex: 1;
          // display: flex;
          // flex-direction: column;

          h1 {
            font-size: 2rem;
            margin-bottom: 1rem;
            color: black;

            .gradientText {
              font-size: 3rem;
              background-image: linear-gradient(45deg, #00D0FF, #007c00);
              -webkit-background-clip: text;
              color: transparent;

              @media (max-width: 768px) {
                font-size: 1.5rem;
              }
              @media (max-width: 546px) {
                font-size: 2rem;
              }
            }

            @media (max-width: 768px) {
              font-size: 1.5rem;
            }
            @media (max-width: 546px) {
              font-size: 20px;
            }
          }

          p {
            font-size: 1.125rem;
            margin-bottom: 1rem;
            line-height: 1.5;

            @media (max-width: 768px) {
              font-size: 1rem;
            }
          }
        }
      }
    }
  }
}
